import myButton from '../../components/my-components/RoundButton';
import {
    number_format
} from '../../components/my-components/formater.js'
export default {
    components: {
        myButton
    },
    props: {
        info: {
            type: Object,
            default: () => ({
                name: '',
                provide: '',
                time: '',
                tel: '',
                num: 0,
                pay: 0,
                subStatus: 0, //订单状态，1：维权
                status: 0, ////0代付款,1代发货，2待收货，3已完成，4已关闭
            })
        }
    },
    computed: {
        showPayType() {
            return (this.info.status != 0 && this.info.status != 4) && this.info.payType == "wx"
        }
    },
    methods: {
        formatePrice(val) {
            return number_format(val, 2, '.', ',')
        },
        clickButton(val) {
            this.$emit('click', {
                type: 'button',
                detail: {
                    val
                }
            })
        },
        clickBill() {
            this.$emit('click', {
                type: 'info',
                detail: {
                    val: this.info
                }
            })
        }
    },
}
